<template>
  <div class="container mt-5">
    <form class="row justify-content-center" @submit.prevent="signIn"> <!-- form 加上事件 -->
      <div class="col-md-6">
        <h1 class="h3 mb-3 font-weight-normal">請先登入</h1>
        <div class="mb-2">
          <label for="inputEmail" class="sr-only">Email address</label>
          <input
            type="email"
            id="inputEmail"
            class="form-control"
            placeholder="Email address"
            required
            autofocus
            v-model="user.username"
          />
        </div>
        <div class="mb-2">
          <label for="inputPassword" class="sr-only">Password</label>
          <input
            type="password"
            id="inputPassword"
            class="form-control"
            placeholder="Password"
            required
            v-model="user.password"
          />
        </div>
        <div class="text-end mt-4">
          <button class="btn btn-lg btn-primary btn-block" type="submit">登入</button>
        </div>
      </div>
    </form>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 預計要送出的資料
      user: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    signIn () {
      // 定義要比對的 API
      const api = `${process.env.VUE_APP_API}admin/signin`
      // 使用 axios 的 post 方法送出 API
      this.$http.post(api, this.user) // 參數：API 路徑、夾帶的資料
        .then((res) => {
          // 取 token 和 expires
          const { token, expired } = res.data // 物件解構賦值
          console.log(token, expired)
          // 加入到 Cookie
          document.cookie = `loginToken=${token}; expires=${new Date(expired)}` // 參數：自訂名稱=token; expires=有效日期
          console.log(res)
        })
    }
  }
}
</script>
